import React, { useState, useRef } from "react";
import { Link } from "react-router-dom";
import { DATA } from "../../DATA";

import "./companyNav.css";

const CompanyNav = ({ style, clickedType, currentPage }) => {
  let tmpArray = [];
  const itemCheck = item => {
    if (tmpArray.indexOf(item.company_type) === -1) {
      tmpArray.push(item.company_type);
      return true;
    }
    return false;
  };

  const uniqueCompanyType = DATA.filter(item => itemCheck(item));

  const [isDown, setIsDown] = useState(false);
  const [startX, setStartX] = useState();
  const [scrollLeft, setScrollLeft] = useState();
  const [clickedTypeLocal, setClickedTypeLocal] = useState();

  const slideBtnMenu = useRef(null);
  const supportsTouch = "ontouchstart" in document.documentElement;

  return (
    <div
      ref={slideBtnMenu}
      className={"сompanyNav"}
      style={{ ...style }}
      //BROWSER ===========================================
      onMouseDown={e => {
        if (!supportsTouch) {
          setIsDown(true);
          setStartX(e.pageX - slideBtnMenu.current.offsetLeft);
          setScrollLeft(slideBtnMenu.current.scrollLeft);
        }
      }}
      onMouseLeave={() => {
        if (!supportsTouch) {
          setIsDown(false);
        }
      }}
      onMouseUp={() => {
        if (!supportsTouch) {
          setIsDown(false);
        }
      }}
      onMouseMove={e => {
        if (!supportsTouch) {
          if (!isDown) return;
          e.preventDefault();
          const x = e.pageX - slideBtnMenu.current.offsetLeft;
          const walk = (x - startX) * 2;
          slideBtnMenu.current.scrollLeft = scrollLeft - walk;
        }
      }}
      //TOUCH =============================================
      onTouchStart={e => {
        if (supportsTouch) {
          setIsDown(true);
          setStartX(
            Math.ceil(e.nativeEvent.touches[0].clientX) -
              slideBtnMenu.current.offsetLeft
          );
          setScrollLeft(slideBtnMenu.current.scrollLeft);
        }
      }}
      onTouchEnd={() => {
        if (supportsTouch) {
          setIsDown(false);
        }
      }}
      onTouchMove={e => {
        if (supportsTouch) {
          const x =
            e.nativeEvent.touches[0].clientX - slideBtnMenu.current.offsetLeft;
          const walk = (x - startX) * 2;
          slideBtnMenu.current.scrollLeft = scrollLeft - walk;
        }
      }}
    >
      <div
        className={
          !clickedTypeLocal ? "companyNavBlock activeBtn" : "companyNavBlock"
        }
        onClick={() => {
          clickedType();
          setClickedTypeLocal();
        }}
      >
        <Link
          to={currentPage}
          className={
            !clickedTypeLocal
              ? "companyNavLink activeBtnText"
              : "companyNavLink"
          }
        >
          <p className="allText">Все</p>
        </Link>
      </div>

      {uniqueCompanyType.length &&
        uniqueCompanyType.map((el, i) => {
          return (
            <div
              className={
                clickedTypeLocal === el.company_type
                  ? "companyNavBlock activeBtn"
                  : "companyNavBlock"
              }
              key={i}
              onClick={e => {
                clickedType(el.company_type);
                setClickedTypeLocal(el.company_type);
              }}
            >
              <Link
                className={
                  clickedTypeLocal === el.company_type
                    ? "companyNavLink activeBtnText"
                    : "companyNavLink"
                }
                to={currentPage + "/" + el.company_type_en}
              >
                <img
                  alt="Icon"
                  className="сompanyNavImg"
                  src={`${process.env.PUBLIC_URL}/img/${el.company_type_en}.png`}
                  width="30"
                  height="30"
                />
                <p className="сompanyNavText">{el.company_type}</p>
              </Link>
            </div>
          );
        })}
    </div>
  );
};

export default CompanyNav;
